
//theme extendable classes

// %transition-10

@for $i from 2 through 50 {
  %transition-#{$i} {
    @include transitionTh(all,#{$i/10}s);
  }
}

// %rounded-10

@for $i from 1 through 100 {
  %rounded-#{$i} {
    @include rounded(#{$i}px);
  }
}

%d-block-full {
  width: 100%;
  float: left;
}


%text-clipped {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: clip;
}

%text-ellipses {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

%user-select-none {
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
}

%cover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
%contain-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
%cover-box {
  @include position(absolute, 0 0 0 0);
}
